#app2 {
  border: 1px solid green;
  width: 50vw;
  height: 50vh;
}
#app2 .tab-bar {
  display: flex;
}
#app2 .tab-bar > li {
  border: 3px slid grey;
  width: 50%;
}
#app2 .tab-bar > li.selected {
  background: rgb(253, 221, 155);
  color: white;
}
#app2 .tab-content {
}
#app2 .tab-content > li {
  display: none;
}
#app2 .tab-content > li.active {
  display: block;
}
